  --sem-color-surface-neutral-highlighted: var(--white);
  --sem-color-surface-neutral-hover: rgba(0, 0, 0, .05);
  --sem-color-surface-primary-active: var(--purple-700);
  --sem-color-surface-primary-background: var(--purple-100);
  --sem-color-surface-primary-default: var(--purple-400);
  --sem-color-surface-primary-hover: var(--purple-500);
  --sem-color-text-disabled: var(--gray-300);
  --sem-color-text-neutral-contrast: var(--black);
  --sem-color-text-neutral-default: var(--gray-900);
  --sem-color-text-neutral-reversed: var(--white);
  --sem-color-text-neutral-subdued: var(--gray-700);
  --sem-color-text-primary-default: var(--purple-400);
  --sem-color-icon-neutral-contrast: var(--black);
  --sem-color-icon-neutral-default: var(--gray-900);
